              </slot>
            </div>
          </div>
          <div class="set_cont">
            <dl class="set_report_val">
              <div>
                <dt> </dt>
                <dd>
                  <ul v-if="edit1">
                    <li v-for="(item,key) in evalItemPolicyList">
                      <input type="radio" :id="'plc'+key" v-model="evalItemPolicy" :value="key" />
                      <label :for="'plc'+key" :title="item.long">
                        <span></span>{{item.short}}
                      </label>
                    </li>
                  </ul>
                  <slot v-else-if="evalItemPolicyList[evalItemPolicy]">
                    {{evalItemPolicyList[evalItemPolicy].short}}
                  </slot>
                </dd>
              </div>
            </dl>
          </div>
        </div>
        <div class="set_auto_box box4" style="height:auto">
          <div class="set_title">
            <h2>/</h2>
            <p>  (/)  .</p>
            <div class="set_tit_btn">
              <slot v-if="edit2">
                <a @click="edit2=false;onSetting()" class="btn_wh"></a>
                <a @click="applyPolicyArea(true)" class="btn_na"></a>
              </slot>
              <slot v-else>
                <a @click="edit2=true" class="btn_gr"></a>
              </slot>
            </div>
          </div>
          <div class="set_cont">
            <dl class="set_report_val">
              <div>
                <dt> ()</dt>
                <dd>
                  <ul v-if="edit2">
                    <li><input type="radio" id="evPDY" v-model.lazy="evalItemPolicySize" value="Y"><label for="evPDY"><span></span></label></li>
                    <li><input type="radio" id="evPDN" v-model.lazy="evalItemPolicySize" value="N"><label for="evPDN"><span></span></label></li>
                  </ul>
                  <slot v-else>{{ evalItemPolicySize === 'Y' ? '' : '' }}</slot>
                </dd>
              </div>
            </dl>
            <dl class="set_report_val">
              <dt> (%)</dt>
              <dd>
                <ul v-if="edit2">
                  <li><input type="radio" id="evPRY" v-model.lazy="evalItemPolicyRatio" value="Y"><label for="evPRY"><span></span></label></li>
                  <li><input type="radio" id="evPRN" v-model.lazy="evalItemPolicyRatio" value="N"><label for="evPRN"><span></span></label></li>
                </ul>
                <slot v-else>{{ evalItemPolicyRatio === 'Y' ? '' : '' }}</slot>
              </dd>
            </dl>
            <dl class="set_report_val surplus" :class="{'conceal':evalItemPolicySize !== 'Y'}">
              <dt>  </dt>
              <dd>
                <ul v-if="edit2">
                  <li><input type="radio" id="evSOY" v-model.lazy="evalItemPolicySizeOnline" value="Y"><label for="evSOY"><span></span></label></li>
                  <li><input type="radio" id="evSON" v-model.lazy="evalItemPolicySizeOnline" value="N"><label for="evSON"><span></span></label></li>
                </ul>
                <slot v-else>{{ evalItemPolicySizeOnline === 'Y' ? '' : '' }}</slot>
              </dd>
            </dl>
            <dl class="set_report_val surplus" :class="{'conceal':evalItemPolicyRatio !== 'Y'}">
              <dt>  </dt>
              <dd>
                <ul v-if="edit2">
                  <li><input type="radio" id="evROY" v-model.lazy="evalItemPolicyRatioOnline" value="Y"><label for="evROY"><span></span></label></li>
                  <li><input type="radio" id="evRON" v-model.lazy="evalItemPolicyRatioOnline" value="N"><label for="evRON"><span></span></label></li>
                </ul>
                <slot v-else>{{ evalItemPolicyRatioOnline === 'Y' ? '' : '' }}</slot>
              </dd>
            </dl>
          </div>
        </div>
        <div class="set_auto_box box4" style="height:auto">
          <div class="set_title">
            <h2></h2>
            <p>   (float) .</p>
            <div class="set_tit_btn">
              <slot v-if="edit4">
                <a @click="edit4=false;onSetting()" class="btn_wh"></a>
                <a @click="applyPolicyCorrection(true)" class="btn_na"></a>
              </slot>
              <slot v-else>
                <a @click="edit4=true" class="btn_gr"></a>
              </slot>
            </div>
          </div>
          <div class="set_cont">
            <dl class="set_report_val">
              <div>
                <dt>  </dt>
                <dd>
                  <ul v-if="edit4">
                    <li><input type="text" id="evCV" v-model.lazy="evalItemPolicyCorrection"><label for="evCV"><span></span></label></li>
                  </ul>
                  <slot v-else>x {{evalItemPolicyCorrection}}</slot>
                </dd>
              </div>
            </dl>
          </div>
        </div>
      </div>
      <div class="set_box_area">
        <div class="set_val_box">
          <div class="set_title">
            <h2>   </h2>
            <p>     .</p>
            <div class="set_tit_btn">
              <slot v-if="edit3">
                <a @click="edit3=false;onSetting()" class="btn_wh"></a>
                <a @click="applyPolicyList(true)" class="btn_na"></a>
              </slot>
              <slot v-else>
                <a @click="edit3=true" class="btn_gr"></a>
              </slot>
            </div>
          </div>
          <div class="set_cont">
            <div class="set_list_area">
              <div class="set_list_box box1">
                <ul>
                  <li v-for="(largeitem, largekey) in evalItemPolicySetList">
                    <div class="set_li">
                      <span class="item_tit">{{largeitem.alias}}</span>
                    </div>
                    <dl v-if="largeitem.item!==null" v-for="(smallitem, smallkey) in largeitem.item">
                      <dt>
                        <div class="set_li">
                          <span class="item_tit" :class="{'conceal':!smallitem.isEval}">{{smallitem.name}}</span>
                          <span class="item_rt">
                              <slot v-if="edit3">
                                <a class="btn" :class="{'btn_bl':smallitem.isEval,'btn_gr':smallitem.isEval}" @click="smallitem.isEval=true"></a>
                                <a class="btn" :class="{'btn_bl':!smallitem.isEval,'btn_gr':smallitem.isEval}" @click="smallitem.isEval=false"></a>
                                &nbsp;<!--
                                <input type="checkbox" :id="'plcset'+largekey+smallkey" v-model="smallitem.isEval" :value=true><label :for="'plcset'+largekey+smallkey"><span></span></label>-->
                              </slot>
                              <span v-else :class="{'conceal':!smallitem.isEval}">{{smallitem.isEval ? '' : ''}}</span>
                            </span>
                        </div>
                      </dt>
                    </dl>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapActions} from 'vuex';
  import Vue from 'vue';
  export default {
    name: 'EvalValue',
    components: {

    },
    data() {
      return {
        evalItemPolicy: 0,
        evalItemPolicyList: [],
        evalItemPolicySetList: [],
        evalItemPolicySize: 'N',
        evalItemPolicyRatio: 'N',
        evalItemPolicyNoUse: 'Y',
        evalItemPolicyCorrection: 1.0,
        evalItemPolicySizeOnline: 'Y',
        evalItemPolicyRatioOnline: 'Y',
        edit1: false,
        edit2: false,
        edit3: false,
        edit4: false
      }
    },
    async mounted() {
      await this.onSetting();
    },
    watch: {
      evalItemPolicySize(newVal) {
        this.evalItemPolicySizeOnline = newVal;
      }, // /    
      evalItemPolicyRatio(newVal) {
        this.evalItemPolicyRatioOnline = newVal;
      }
		},
    methods: {
      ...mapActions(['getMediaPolicyAPI']),
      async onSetting() {
        await this.applyPolicy();
        await this.applyPolicyArea();
        await this.applyPolicyList();
        await this.applyPolicyCorrection();
      },

      async applyPolicyArea(arg) { // 2   
        const params = new FormData();
        params.append('m', 'z');
        if (this.edit2) {
          params.append('as', this.evalItemPolicySize); // 
          params.append('ar', this.evalItemPolicyRatio); // 
          params.append('aso', this.evalItemPolicySizeOnline); // 
          params.append('aro', this.evalItemPolicyRatioOnline); // 
        }

        const result = await this.getMediaPolicyAPI(params);
        if (result && result.success && result.final) {
          this.evalItemPolicySize = result.final['SIZE'];
          this.evalItemPolicyRatio = result.final['RATIO'];
          this.evalItemPolicySizeOnline = result.final['SIZE_ONLINE'];
          this.evalItemPolicyRatioOnline = result.final['RATIO_ONLINE'];
          if (this.edit2) {
            alert('/   .');
          }
          this.edit2 = false;
        } else if (arg) {
          this.$eventBus.$emit('kickOut');
        }
      },
      async applyPolicy(arg) {
        const params = new FormData();
        params.append('m', 'v');
        if (arg) params.append('q', this.evalItemPolicy);
        const result = await this.getMediaPolicyAPI(params);
        if (result && result.final && result.final_list && result.success) {
          this.evalItemPolicy = result.final;
          this.evalItemPolicyList = result.final_list;
          if (arg) alert('   .');
          this.edit1 = false;
        } else if (arg) {
          this.$eventBus.$emit('kickOut');
        }
      },
      async applyPolicyList(arg) {
        const params = new FormData();
        params.append('m', 'l');
        if (arg) {
          let list = [];
          params.append('q', JSON.stringify(this.evalItemPolicySetList));
        }
        const result = await this.getMediaPolicyAPI(params);
        if (result && result.final && result.success) {
          this.evalItemPolicySetList = result.final;
          if (arg) alert('   .');
          this.edit3 = false;
        } else if (arg) {
          this.$eventBus.$emit('kickOut');
        }
      },
      async applyPolicyCorrection(arg) {
        const params = new FormData();
        params.append('m', 'x');
        if (arg) {
          params.append('q', this.evalItemPolicyCorrection);
        }
        const result = await this.getMediaPolicyAPI(params);
        if (result && result.final && result.success) {
          this.evalItemPolicyCorrection = result.final;
          if (arg) alert('   .');
          this.edit4 = false;
        } else if (arg) {
          this.$eventBus.$emit('kickOut');
        }
      }
    }
  }
</script>
