							<h2>  </h2>
							<p>*       . ( : )</p>
							<div class="set_tit_btn">
								<slot v-if="edit">
									<!--<a @click="onSetting()" class="btn_gr"></a>-->
									<a @click="apply()" class="btn_na"></a>
