      dataIntersection(rawCrossData, crossLength, newS, ev2ObjIdx0, ev2ObjIdx1, ev2ObjIdx2, ev2ObjIdx3) {
        if (crossLength === 1) { 
          this.settingRawCrossData(rawCrossData, crossLength, newS, 'r'+ev2ObjIdx0);
